.FeedItem-Image {
  grid-area: content;
  grid-column: 1 / 3;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  background: #f4f4f4;
  position: relative; }
  .FeedItem-Image img {
    -o-object-fit: cover;
       object-fit: cover;
    width: 100%;
    border-radius: 4px; }
  .FeedItem-Image:after {
    content: '';
    display: block;
    position: absolute;
    background-color: transparent;
    width: 100%;
    height: 100%;
    -webkit-transition: background-color .2s;
    -o-transition: background-color .2s;
    transition: background-color .2s; }



