.FeedItem {
  display: grid;
  position: relative;
  grid-row-gap: 8px;
  grid-column: auto / span 6;
  grid-template-areas: "header header" "content controls";
  grid-template-rows: -webkit-min-content minmax(10rem, auto);
  grid-template-rows: min-content minmax(10rem, auto); }



